	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import jihuaqianding from '@/views/jihuaqianding/list'
	import lvyouxinxi from '@/views/lvyouxinxi/list'
	import yonghu from '@/views/yonghu/list'
	import lvyoujihua from '@/views/lvyoujihua/list'
	import orders from '@/views/orders/list'
	import lvxingshe from '@/views/lvxingshe/list'
	import config from '@/views/config/list'
	import users from '@/views/users/list'
	import lvxingsheRegister from '@/views/lvxingshe/register'
	import lvxingsheCenter from '@/views/lvxingshe/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/lvxingsheCenter',
			name: '旅行社个人中心',
			component: lvxingsheCenter
		}
		,{
			path: '/news',
			name: '公告信息',
			component: news
		}
		,{
			path: '/jihuaqianding',
			name: '计划签订',
			component: jihuaqianding
		}
		,{
			path: '/lvyouxinxi',
			name: '旅游信息',
			component: lvyouxinxi
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/lvyoujihua',
			name: '旅游计划',
			component: lvyoujihua
		}
		,{
			path: '/orders',
			name: '订单管理',
			component: orders
		}
		,{
			path: '/lvxingshe',
			name: '旅行社',
			component: lvxingshe
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		]
	},
	{
		path: '/lvxingsheRegister',
		name: '旅行社注册',
		component: lvxingsheRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
